import React from 'react';
import { Formik, Form } from 'formik';
import classNames from 'classnames';
import { useSubmit } from 'react-router-dom';

import styles from './styles.module.scss';
import { initialValues, dateRangeOptions } from './params';

import Button from '../Button/Button';
import TextField from '../TextField/TextField';
import SelectField from '../SelectField/SelectField';
import Checkbox from '../Checkbox/Checkbox';
import DateRangePickerField from '../DateRangePickerField/DateRangePickerField';

import { SEARCH_RESULT_URL } from '../../constants';
import { searchValidationSchema } from '../../utils/validation/schema';

const SearchForm = ({ className }) => {
  const formClass = classNames(styles.form, className);
  const submit = useSubmit();

  const handleOnSubmit = async (values, { setSubmitting }) => {
    submit(values, {
      method: 'post',
      action: SEARCH_RESULT_URL,
    });

    setSubmitting(false);
  };

  return (
    <Formik
      initialValues={initialValues}
      validationSchema={searchValidationSchema}
      onSubmit={handleOnSubmit}
    >
      {({ dirty, isValid, isSubmitting }) => (
        <Form className={formClass}>
          <div className={styles.col}>
            <TextField
              label="  *"
              id="inn"
              name="inn"
              placeholder="10 "
            />

            <SelectField label=":" id="tonality" name="tonality">
              <option value="any"></option>
              <option value="positive"></option>
              <option value="negative"></option>
            </SelectField>

            <TextField
              label="    *"
              id="limit"
              name="limit"
              placeholder=" 1  1000"
            />

            <DateRangePickerField
              label="  *"
              options={dateRangeOptions}
            />
          </div>

          <div className={styles.col}>
            <Checkbox label="  " name="maxFullness" />

            <Checkbox
              label="  -"
              name="inBusinessNews"
            />

            <Checkbox label="   " name="onlyMainRole" />

            <Checkbox
              label="   -"
              name="onlyWithRiskFactors"
            />

            <Checkbox
              label="   "
              name="includeTechNews"
            />

            <Checkbox
              label="   "
              name="includeAnnouncements"
            />

            <Checkbox label="  " name="includeDigests" />

            <div className={styles.submitGroup}>
              <Button
                className={styles.submit}
                type="submit"
                color="primary"
                disabled={!dirty || !isValid || isSubmitting}
              >
                
              </Button>

              <p className={styles.mark}>*    </p>
            </div>
          </div>
        </Form>
      )}
    </Formik>
  );
};

export default SearchForm;
